/* global document, Office, Word */

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("run").onclick = run;
  }
});

export async function run() {
  return Word.run(async (context) => {
    /**
     * Insert your Word code here
     */

    //insert a paragraph at the end of the document.
    const paragraph = context.document.body.insertParagraph("Hello World", Word.InsertLocation.end);

    //change the paragraph color to blue.
    paragraph.font.color = "blue";

    await context.sync();

    Office.context.document.getFilePropertiesAsync(function (result) {
      //context.document.body.insertParagraph("xx", Word.InsertLocation.end);
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          const fileProperties = result.value;

          console.log("File Properties:");
          console.log("URL:", fileProperties.url);

          const paragraph = context.document.body.insertParagraph(fileProperties.url, Word.InsertLocation.start);
          

          // Display properties in the task pane
        } else {
          console.error("Error getting file properties:", result.error.message);
        }
      });

      await context.sync();
  });
}
